@import url('https://fonts.googleapis.com/css?family=Indie+Flower');
@import url('https://fonts.googleapis.com/css?family=Playfair+Display');
@import url('https://fonts.googleapis.com/css?family=Crimson+Text');
@import url('https://fonts.googleapis.com/css?family=Josefin+Slab');

* {
	box-sizing: border-box;
	font-family: 'Rubik', sans-serif;
}

body {
	background-color: white;
	display: flex;
	flex-direction: column;
}

.principal-content {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
}
.principal-title {
	width: 100%;
	text-align: center;
	font-family: 'Indie Flower';
	color: #FFAB33;
	font-size: 3em;
	font-weight: bold;
	margin-top: 170px;
	display: flex;
	justify-content: center;
}
.personalizar {
	display: flex;
	flex-direction: row;
	border: 3px solid #006D9D;
	border-radius: 15px;
	justify-content: center;
	align-items: center;

}
.personalizar li {
	display: flex;
	flex-direction: column;
	justify-content: center;
	padding: 10px;
	text-align: center;
	font-weight: bold;
}
.personalizatucv {
	width: 100%;
	position: relative;
	display: flex;
	padding: 15px;
	box-sizing: border-box;
	justify-content: center;
	align-items: center;
	justify-content: center;
}
.personalizar img {
	padding: 10px;
	height: 90px;
	width: 90px;
}
/*Pagina principal*/
.background {
	height: 100vh;
	background-image: url(../images/background3.jpg);
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.title {
	font-family: 'Indie Flower';
	font-size: 4em;
}
li {
	list-style: none;
}
.text {
	color: white;
	text-decoration: none;
}
.text:hover {
	font-weight: bold;
	color: #8b0061;
}
.principal-logo {
	border-bottom: 5px solid white;
	margin-bottom: 50px;
}
.principal-logo img {
	width: 250px;
}
.principal-logo a {
	text-decoration: none;
}
/*FIN PÁGINA INICIO*/

/*PREVIO CV*/
.cv {
	display: flex;
	box-sizing: border-box;
	flex-direction: column;
	width: 95%;
	max-width: 600px;
	margin: auto;
	font-size: 15px;
	line-height: 1.5;
	background-color: #FFFFE2;
	height: auto;
	margin-bottom: 30px;
	box-shadow: 4px 4px 1px #B4B6B2;
}
.prev-info-personaldates {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
	padding: 10px;
	background-color: #ffe7b9;
}
.prev-photo {
	width: 30%;
	min-width: 120px;
}
.prev-photo img{
	border-radius: 50%
}
.prev-list-personal{
	width: 65%;
}
.cut-url{
	overflow-wrap: break-word;
	word-wrap: break-word;
}
.prev-description,
.prev-education,
.prev-professionalexperience,
.prev-skills,
.prev-interestdetails {
	padding: 10px;
}
.prev-titles {
	font-size: 15px;
	font-weight: bold;
	margin-top: 0px;
	width: 100%;
	text-align: left;
}
.camps-titles{
	font-weight: bold;
	display: inline;
}


/*MEDIAQUERIES*/
@media all and (min-width: 700px) {
	.title {
		font-size: 6em;
	}

	.principal-logo img {
		width: 400px;
	}

	.principal-title {
		font-size: 4em;
	}
	.personalizar {
		max-width: 470px;
	}
	.personalizar img {
		height: 125px;
		width: 125px;
	}
	.prev-photo {
		width: 20%;
	}
	.prev-list-personal{
		width: 70%;
	}
}

@media all and (min-width: 900px) {

	.body {
		display: flex;
		flex-wrap: wrap;
	}
	.title {
		font-size: 7em;
	}
	.principal-logo img {
		width: 500px;
	}
	.principal-content {
		flex-direction: row;
		flex-wrap: wrap;
	}
	.text-principal {
		width: 100%;
	}

	.cv{
		width: 55%;
		margin-top: 30px;
		margin-bottom: 20px;
	}
	.prev-photo {
		width: 24%;
	}
	.prev-list-personal{
		width: 74%;
	}
}
