.input-form-buttoms {
  position: relative;
  display: flex;
  justify-content: center;
  padding: 15px;
}

.input-form-buttoms input[type="button"],.input-form-buttoms input[type="reset"] {
  width: 30%;
  height: 35px;
  background-color: #fcac0c;
